
/*
        时间格式化工具
        把Long类型的yyyy-MM-dd日期还原yyyy-MM-dd格式日期
    */

let format = {
  dateFormatUtil: function (longTypeDate) {
    let dateTypeDate = ''
    let date = new Date()
    date.setTime(longTypeDate)
    dateTypeDate += date.getFullYear() // 年
    dateTypeDate += '-' + this.getMonth(date) // 月
    return dateTypeDate
  },

  // 返回 01-12 的月份值
  getMonth (date) {
    let month = ''
    month = date.getMonth() + 1 // getMonth()得到的月份是0-11
    if (month < 10) {
      month = '0' + month
    }
    return month
  },

  // 返回01-30的日期
  getDay (date) {
    let day = ''
    day = date.getDate()
    if (day < 10) {
      day = '0' + day
    }
    return day
  }
}

export default format



