<template>
  <div class="main_container" :style="{height:height+'px'}">

    <el-container class="top_container">
      <el-header class="header">记事</el-header>
      <el-container style="width: 100%;height: 100%">
        <el-aside class="aside" width="30%">
          <img :src="head_img" class="headImg">
          <span class="nick_name">{{nick_name}}</span>
          <span class="personal_sign">{{personal_sign.length>0?personal_sign:'so lazy'}}</span>

          <el-menu class="sideMenu">
            <el-menu-item index="1" class="silde-sub-item" @click="gotoCreateType">
              <i class="el-icon-news" ></i>
              <span>创建分类</span>
              <i class="el-icon-arrow-right inline_sty"></i>
            </el-menu-item>
            <el-menu-item index="2" class="silde-sub-item" @click="gotoCreateArticle">
              <i class="el-icon-document"></i>
              <span>创建文章</span>
              <i class="el-icon-arrow-right inline_sty" ></i>
            </el-menu-item>

          </el-menu>

        </el-aside>
        <el-container>
          <el-main class="main">

            <el-menu :unique-opened="true">
              <el-submenu v-for="(item,index) of types" :index="index.toString()" :key="index">
                <template slot="title"><i class="el-icon-document"></i>{{item.title}}</template>
                <el-menu :unique-opened="true" style="padding-left: 26px">
                  <el-submenu v-for="(child1,index) of item.listSet" :index="index.toString()" :key="index">
                    <template slot="title"><i class="el-icon-date"></i>{{getFormatDate(child1[0].createTime)}}
                    </template>
                    <el-menu-item-group v-for="(chid,index) of child1" :key="index">
                      <el-menu-item :index="index.toString()" @click="gotoArticle(chid.id)">{{chid.title}}
                      </el-menu-item>
                    </el-menu-item-group>
                  </el-submenu>
                </el-menu>
              </el-submenu>
            </el-menu>
          </el-main>
        </el-container>
      </el-container>
      <el-footer class="footer"></el-footer>
    </el-container>

  </div>
</template>

<script>

import {query} from '../../api/login'
import format from '../../util/DateFormatUtil'

export default {
  name: 'main',
  data () {
    return {
      height: '',
      head_img: '',
      nick_name: '',
      personal_sign: '',
      age: '',
      types: []
    }
  },
  created: function () {
    this.height = window.innerHeight
    query.queryInfo(this.$store.getters.userName, res => {
      let tempData = JSON.parse(JSON.stringify(res))
      this.head_img = tempData.data.headImg
      this.nick_name = tempData.data.nickName
      this.personal_sign = tempData.data.personalSign
      this.age = tempData.data.age
      console.log('user: ' + JSON.stringify(res))
    })

    query.queryAllTypes(this.$store.getters.userName, res => {
      let tempData = JSON.parse(JSON.stringify(res))
      this.types = tempData.data.diaryTypes
      console.log('temp: ' + JSON.stringify(this.types))
    })
  },
  methods: {
    getFormatDate (longDate) {
      return format.dateFormatUtil(longDate)
    },
    gotoArticle (id) {
      this.$store.commit('setCurrentAId', id)
      this.$router.push('article')
    },
    gotoCreateType () {
      this.$router.push('create_type')
    },
    gotoCreateArticle () {
      this.$router.push('create_article')
    }
  }
}
</script>

<style scoped>

  .inline_sty{
    text-align: right;
    width: 59%;
    display: inline-block;
  }
  .silde-sub-item{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .sideMenu {
    margin-right: 20px;
    margin-top: 20px;
  }

  .headImg {
    width: 30%;
    min-width: 100px;
  }

  .aside {
    height: 100%;
    background-color: antiquewhite;
    display: flex;
    flex-direction: column;
    align-items: left;
  }

  .main {
    width: 100%;
    background-color: antiquewhite;
  }

  .header {
    width: 100%;
    height: 80px;
    background-color: bisque;
    margin-top: 1px;
  }

  .footer {
    width: 100%;
    height: 80px;
    background-color: burlywood;
  }

  .personal_sign {
    display: inline-block;
    text-align: left;
    margin-left: 10px;
  }

  .nick_name {
    display: inline-block;
    text-align: left;
    margin-left: 10px;
    margin-top: 10px;
  }

  .top_container {
    width: 70%;
    height: 100%;
  }

  .main_container {
    width: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

</style>



